
import HomePage from '../pages/home.f7.html';
import ThemePage from '../pages/theme.f7.html';
import ObjectPage from '../pages/object.f7.html';
import axutils from './ax-utils';
import store from './store';

var routes = [
  {
    path: '/',
    component: HomePage,
  },

  {
    path: '/theme/:id',
    async : function ( { router, to, resolve } )
    {
      var app = router.app;
      app.preloader.show();
      var id = to.params.id;

      const query = `{
        masterNarrative: narrativeById(_id: ${id}) {
          title
          description
        }
        subthemes: narratives(filter: {masterNarrative: ${id}}) {
            _id
            title
            description
            mainImage {
              url(width: 100, height: 100, smart: true)
            }
          }
        }`;

        axutils.LoadGQL ( query, (response) => 
        {  
            for ( var theme of response.subthemes )
            {
                theme.title = axutils.CleanTitle(theme.title);
            }

            response.masterNarrative.title = axutils.CleanTitle ( response.masterNarrative.title );

            app.preloader.hide();
            resolve( {  component: ThemePage, },
              { props: { masterNarrative : response.masterNarrative, subthemes: response.subthemes } }
            );
            
        }, console.error );
    }
  },

  {
    path: '/object/:id',
    async : function ( { router, to, resolve } )
    {
      var app = router.app;
      app.preloader.show();
      var id = to.params.id;

      const query = `
      {
        object: objectById(_id: ${id})
        {
          _id
          displayTitle
          summary
          description
          images(limit: 1)
          {
              _id
              width
              height
              thumb: url(width:256, height:256)
              image: url(width:1024, height:1024)
          }
        }
      }`;

        axutils.LoadGQL ( query, (response) => 
        {  
            console.log(response.object);
            response.object.displayTitle = axutils.CleanTitle ( response.object.displayTitle );

            app.preloader.hide();
            resolve( {  component: ObjectPage, },
              { props: { object : response.object } }
            );
            
        }, console.error );
    }
  }
];

export default routes;