
import { createStore } from 'framework7';
import axutils from './ax-utils';

const store = createStore({
    state: {
        masterNarrative : {},
        themes : [],
    },
    getters: {
        masterNarrative({ state }) {
            return state.masterNarrative;
        },

        themes({ state }) {
            return state.themes;
        }
        
    },
    actions: {
        addProduct({ state }, product) {
            state.products = [...state.products, product];
        },

        getThemes ( { state } )
        {
            const query = `
            {
                masterNarrative: narrativeById(_id: 7654) {
                  title
                  description
                  mainImage {
                    url(width: 100, height: 100, smart: true)
                  }
                }
                
                themes: narratives(filter: {masterNarrative: 7654}) {
                  _id
                  title
                  mainImage {
                    url(width: 100, height: 100, smart: true)
                  }
                }
            }`;

            axutils.LoadGQL ( query, (response) => 
            {  
                for ( var theme of response.themes )
                {
                    // Strip leading ordinal 0n - xxx
                    var title = theme.title;
                    if ( title.indexOf ( " - " ) > -1 ) title = title.split ( " - ")[1];
                    theme.title = title;
                }
                state.masterNarrative = response.masterNarrative;
                state.themes = response.themes;
            }, console.error );
              
        }
    },

})
export default store;
